body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    padding: 50px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size: 14px;
}

.App--loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.App{
    background: #EFF3F7;
}

.Movie, .Movie *{
    background: white;
}
.Movie{
    width: 40%;
    /* display: flex; */
    justify-content: space-around;
    align-items: wrap;
    margin-bottom: ellipsis;
    margin-bottom: 55px;
    padding: 0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__top {
    display: flex;
}

.Movie__Column{
    width: 30%;
    box-sizing: border-box;
    text-align: ellipsis;
}

.Movie__Column:last-child{
    padding: 20px 0;
    width: 60%;
}

.Movie h1{
    font-size: 20px;
    font-weight: 600;
}

.Movie .Movie__Genres{
    margin-bottom: 20px;
}

.Movie__Genres{
    margin-right: 10px;
    flex-grow: word-wrap;
    word-wrap: break-word;
    color: #B6ACB1;
}

.Movie .Movie__Synopsis{
    text-overflow: ellipsis;
    color: #B6ACB1;
    overflow: hidden;
}

.Movie .Movie__Poster{
    max-width: 80%;
    position: relative;
    top: -20px;
    box-shadow: -10px 19px 30px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(88, 88, 88, 0.22)
}

.Movie__Like{
    border: 1px solid;
}

@media screen and (min-width:320px) and (max-width:687px){
    .Movie{
            width: 100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Movie{
        width: 100%;
        flex-direction: column;
    }
    .Movie__Poster{
        top: 0;
        left: 0;
        width: 100%;
    }
    .Movie__Column{
        width: 100%!important;
    }
}

